<template>
  <v-container
    id="upgrade"
    fluid
    tag="section"
  >
    <v-row justify="center">
      <v-col
        cols="12"
        md="12"
      >
        <base-material-card
          color="primary"
          title="Orçamentos"
        >
          <v-btn
            color="primary"
            title="Cadastrar Novo Orçamento"
            to="projetos/novo"
            small
          >
            Novo Orçamento
          </v-btn>
         <!-- <v-btn
            color="primary"
            title="Cadastrar Novo Orçamento"
            small
          >
            Buscar Orçamentos
          </v-btn> -->
          <v-simple-table style="background: none">
            <thead>
              <tr>
                <th>
                  <h2>Id</h2>
                </th>
                <th>
                  <h2>Produto</h2>
                </th>
                <th>
                  <h2>Data</h2>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(proj, i) in projetos"
                :key="i"
              >
                <td> {{ proj.neoId }} </td>
                <td> {{ proj.produto.descricao }} </td>
                <td> {{ proj.dataSolicitacao }}</td>
                <td>
                  <v-btn
                    color="primary"
                    title="Editar Orçamento"
                    small
                  >
                    Editar
                  </v-btn>
                  <v-btn
                    color="warning"
                    title="Excluir"
                    @click="removeProject(proj.neoId)"
                    small
                  >
                    Excluir
                  </v-btn>
                </td>
              </tr>
            </tbody>
          </v-simple-table>
        </base-material-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import axios from 'axios'

  export default {
    name: 'Hidroponia',
    data: function () {
      return {
        urlbase: 'http://127.0.0.1:8080/fusion/custom/',
        projetos: [],
        showModal: false,
        formPage: 1,
      }
    },
    created () {
      this.loadProjects()
    },
    methods: {
      loadProjects () {
        this.projetos = []
        axios.get(`${this.urlbase}customApi.jsp?entity=HidroponiaProdutos`)
          .then(({ data }) => {
            this.projetos = data
          }, (error) => {
            this.writeLog(error)
            this.loginError = true
          })
      },
      removeProject (projectId) {
        const remove = window.confirm('Deseja realmente excluir?')

        if (remove) {
          axios.post(`${this.urlbase}persistApi.jsp?projectId=${projectId}&action=delete`)
            .then(({ data }) => {
              this.loadProjects()
            }, (error) => {
              this.writeLog(error)
              this.loginError = true
            })
        }
      },
    },
  }
</script>

<style lang="sass">
  #upgrade
    .v-data-table
      th, td
        border: none !important
</style>
